<template>
  <section
    class="
      w-full
      h-auto
      flex flex-col
      justify-center
      items-center
      py-20
      sm:py-12
      text-white
      container
      mx-auto
      relative
    "
  >
    <BlurryBackground />

    <main class="z-10">
      <div class="w-28 h-28 rounded-full bg-mud p-2 overflow-hidden">
        <img
          class="w-28 h-full sm:w-28 border-4 border-dark rounded-full"
          :src="imageLink"
        />
      </div>
    </main>
    <main class="z-10 py-8 md:py-0">
      <p class="text-center font-DMSan font-semibold text-lg py-5">
        Hi, I am Atif ðŸ‘‹
      </p>
      <p
        class="
          sm:text-4xl
          text-2xl
          tracking-wide
          leading-loose
          sm:leading-relaxed
          font-bold font-DM
          text-center
        "
      >
        Build and Design Modern <br />
        Websites with
        <span class="text-green relative"
          >Vue/Nuxt
          <img
            id="img1"
            class="
              absolute
              -bottom-3
              opacity-0
              left-0
              transition-opacity
              duration-500
            "
            src="../assets/linegreen2.svg"
          />
        </span>
        and
        <br />
        use
        <span class="text-yellow-400 relative"
          >Firebase
          <span>
            <img
              id="img2"
              class="
                absolute
                -bottom-3
                opacity-0
                left-0
                transition-opacity
                duration-500
              "
              src="../assets/line2.svg"
            />
          </span>
        </span>
        for backend
      </p>
    </main>
    <main class="px-1">
      <Buttons />
    </main>
  </section>
</template>

<script>
import { db } from "../firebase/config";
import { collection, getDocs } from "firebase/firestore";
import Buttons from "./Buttons.vue";
import BlurryBackground from "./BlurryBackground.vue";
window.addEventListener("load", function () {
  setTimeout(() => {
    img1.classList.remove("opacity-0");
  }, 3500);
  setTimeout(() => {
    img2.classList.remove("opacity-0");
  }, 4000);
});

export default {
  name: "Hero",
  components: { Buttons, BlurryBackground },
  data: () => {
    return {
      imageLink: "https://i.postimg.cc/KzCkm0kK/profile-min.jpg",
    };
  },
  beforeMount() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      const Data = collection(db, "Profile");
      const data = await getDocs(Data);
      data.forEach((doc) => {
        this.imageLink = doc.data().link;
      });
    },
  },
};
</script>
